export default format;

function isLit (node) {
	return ["keyword", "number", "literal", "string"].includes(node.type);
}

function format (node, stack = []) {
	const children = node.children || [];
	let result = "";
	let lastSignificant;
	let brk = false;

	{if (node.type === "brackets") {
		result += node.data.open;
	}}
	for (let i = 0; i < children.length; i++) {
		const child = children[i];
		const prevChild = children[i - 1];
		const nextChild = children[i + 1];
		const significant = ["comment", "linebreak", ";"].includes(child.type) ? null : child;

		if (child.type === ";" || (child.type === "linebreak" && !brk)) {
			brk = child.type;
		}

		if (significant && lastSignificant) {
			const noSemicolon = brk === "linebreak" && (
				lastSignificant.type === "operation" || lastSignificant.type === "," ||
				significant.type === "operation" || significant.type === ",");
			if (brk && !noSemicolon) {
				result += ";";
			}
		}
		if (child.type === "brackets") {
			stack.push(node);
			result += format(child, stack);
			stack.pop();
		}
		else {
			if (child.data && child.data.val && significant) {
				const space = prevChild && isLit(prevChild) && isLit(child) ? " " : "";
				result += space + child.data.val;
			}
		}

		if (significant) {
			lastSignificant = significant;
			brk = false;
		}
	}
	if (node.type === "brackets") {
		result += node.data.close;
	}
	return result;
}
